.Images {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  z-index: 10;
}
.Div {
  position: relative;
  min-width: 300px;
  height: 300px;
}
.Modal {
  background-color: rgba(31, 29, 29, 0.596);
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  color: white;
}

.Img {
  min-width: 300px;
  height: 300px;
}
@media (max-width: 900px) {
  .Images {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 500px) {
  .Images {
    grid-template-columns: 1fr;
  }
}
