.Images {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  z-index: 10;
}

.Img {
  min-width: 300px;
  height: 300px;
}
@media (max-width: 900px) {
  .Images {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 500px) {
  .Images {
    grid-template-columns: 1fr;
  }
}
